<template>
  <div>
    <LoHeader class="lo_header" title=""></LoHeader>
    <div class="lo_content_box" >
      <mu-load-more class="lo_content" :refreshing="refreshing" :loading="loading" style="width: 100%;"
                    @refresh="refresh"  >
        <mu-container class="main">

          <mu-expansion-panel style="width: 100%" :expand="true">
            <div slot="header"></div>
            <div class="shuju">
              <div  class="shuju_01" @click="goLink('SaveMoneyApplyList')" >
                <div></div>
                <div class="shuju_01_s">{{ MainData.SaveMoney }}/{{ MainData.SaveMoneying }}</div>
              </div>

              <div  class="shuju_01" @click="goLink('DrawMoneyApplyList')" >
                <div></div>
                <div class="shuju_01_s">{{ MainData.DrawMoney }}/{{ MainData.DrawMoneying }}</div>
              </div>

              <div class="shuju_01" >
                <div></div>
                <div class="shuju_01_s">{{ MainData.Gold }}</div>
              </div>
              <div  class="shuju_01" >
                <div></div>
                <div class="shuju_01_s">{{ MainData.Profit }}</div>
              </div>
            </div>

          </mu-expansion-panel>

          <mu-expansion-panel  :expand="true">
            <div slot="header"></div>
            <v-chart style="width: 100%" :options="option" theme="light"></v-chart>
          </mu-expansion-panel>


          <!--      <mu-expansion-panel>-->
          <!--        <div slot="header"></div>-->
          <!--        -->
          <!--      </mu-expansion-panel>-->
        </mu-container>
      </mu-load-more>
    </div>

  </div>
</template>

<script>
import LoHeader from '../../components/LoHeader.vue'
import ECharts from "vue-echarts";
import "echarts/lib/chart/bar";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import "echarts/lib/component/markPoint";
import "echarts/lib/component/markLine";
import "echarts/lib/component/graphic";
import {Get7DayBetYieldChart, GetMainData} from '../../api/Report';

export default {
  name: "home",
  components: {
    LoHeader, 'v-chart':ECharts
  },
  data() {
    return {
      aa:true,
      option : {
        legend: {
          data:['', '1']
        },
        color: ['#3398DB'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 
            type: 'line'        // 'line' | 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '',
            type: 'bar',
            barWidth: '20%',
            data: [10, 52, 200, 334, 390, 330, 220]
          }, {
            name: '1',
            type: 'bar',
            barWidth: '20%',
            data: [10, 52, 200, 334, 390, 330, 220]
          }
        ]
      },
      MainData:{SaveMoney:0,
        SaveMoneying:0,
        DrawMoney:0,
        DrawMoneying:0,
        Gold:0,
        Profit:0,
      },
      refreshing: false,
      loading: false,
    }
  },
  methods: {
    change(){

    },
    reloadChartData(fun){
      Get7DayBetYieldChart(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {

          this.option.legend.data = new Array(obj.datasets[0].label,obj.datasets[1].label);
          this.option.xAxis[0].data = obj.labels;
          let series1 = {
            name: obj.datasets[0].label,
            type: 'bar',
            barWidth: '20%',
            data:obj.datasets[0].data,
          }
          let series2 = {
            name: obj.datasets[1].label,
            type: 'bar',
            barWidth: '20%',
            data:obj.datasets[1].data,
          }
          this.option.series = new Array(series1,series2);
          // console.log(this.option);
          fun();

          resolve();
        } else {
        }
      }).catch(error => {
      });
    },
    reloadMainData(fun){
      GetMainData(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          this.MainData = obj;
          fun();
          resolve();
        } else {
        }
      }).catch(error => {
      });
    },
    reloadAllData(){
      this.reloadChartData();
      this.reloadMainData();
    },
    refresh(){
      this.reloadChartData();
    },
    goLink(u) {
      this.$router.push({ path: this.redirect || u});
    },
  },
  created() {
    this.reloadAllData();
  },
  mounted() {
    if (this.timer) {
      clearInterval(this.timer)
    } else {
      this.timer = setInterval(this.reloadAllData,30000);
    }
  },
  destroyed() {
    clearInterval(this.timer);
  }
}
</script>

<style scoped>
@import url('../../assets/css/main.css');

.page {
  width: 100%;
  min-height: 100vh;
  background-color: #ffffff;
  overflow: hidden;
  display:block;
}
.main {
  margin-top: 8px;
  width: 100%;
  /*margin-top: 1.4rem;*/
  margin-bottom: 1.4rem;
}
.bbb{
  border: 1px solid red;
}

.shuju {
  display: flex;
  flex-wrap: wrap;
}

.shuju_01 {
  width: 50%;
  text-align: center;
  padding: 18px 10px;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 25px;
  font-size: 14px;
  border-right: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.shuju_01_s {
  color: #EB511B;
  font-weight: 800;
  font-size: 16px;
}

.no {
  border-right: none;
}

</style>



